import { Component, OnInit, Output, EventEmitter} from '@angular/core';
import { DataService } from '../services/data.service';
import { ActivatedRoute } from '@angular/router';

import { Islide } from '../interfaces/Islide';
import { MapData } from '../interfaces/map-data';
import { Zone, Region, Icon, Slide, Poster, Cover, Glass} from '../classes/zone';
import { ImgData} from '../interfaces/img-data';
import { ZoneCategory } from '../interfaces/zone-category';

@Component({
  selector: 'app-interaction',
  templateUrl: './interaction.component.html',
  styleUrls: ['./interaction.component.css']
})
export class InteractionComponent implements OnInit {
    @Output() soundLoaded = new EventEmitter<string>();
    slide: any;
    zoneLoader : Zone[];
    zones: any;
    description: string;
    imgData: any;
    imgUrl: string;
    songUrl:string;
    youTubeId : string = "";
    commentUrl:string;
    name: string;
    zoneCategories: ZoneCategory[];
    mode : string;
    imgCount : number;
    imgCounter : number = 0;
    
  constructor(protected _activatedRoute: ActivatedRoute, protected _dataService: DataService) { 
  this.zones=[];
      this.zoneCategories=[];
this.mode="interaction";
  }


  ngOnInit() {
   // console.log("route at component: "+this._activatedRoute.snapshot.params['map']);
   //   console.log("selected zone: "+this._activatedRoute.snapshot.params['zone']);
      
      //this._dataService.askOxford("","el");
      
      let map=this._activatedRoute.snapshot.params['map'];
      let zone=this._activatedRoute.snapshot.params['zone'];
      let ref=this;
    this._dataService.fetchJson(map).subscribe(function(data : MapData){
      //  console.log(data);
       let imgData=data["imgData"];
        ref.imgData=imgData;
       // console.log(ref.imgData);
        
    ref.imgCount=data.slides.filter((s)=>s.fields.icon.value!="").length; 
        console.log("expecting " + ref.imgCount + "images total");
        
      ref.zoneLoader=ref.processIcons(data);
      //  console.log(ref.zones);
  ref.displayZones();
      let catList=ref.zones.map((zone)=>zone.category);
     // this.cats=new Set(catList);
      if(zone){
          ref.displayClicked(this.zones[zone]);
      }    
        
        
        
    });
       // let id=<string>this._activatedRoute.snapshot.params['zid'];
  
      
      
  }
    
displayClicked(e){
    //console.log("clicked");
    //console.log(e);
    this.name=e.word;
    this.description=e.description;
    this.imgUrl=e.image;
    
        if(e.musicSource=="youTube"){
            this.youTubeId=e.music;
            this.songUrl="";
        }else{
            this.songUrl="assets/audio/music/"+e.music;
            this.youTubeId="";
        }
      
        
    
    
    if(e.fields.comment.value){
       this.commentUrl="assets/audio/voice/"+e.fields.voice.value; 
    }
    
    this.soundLoaded.emit(this.songUrl);
}
    
       processIcons(slideSet){
        let ref=this;
        var result = [];
         let categories = {};
           
        slideSet.slides.forEach(function(slide){
          //  console.log("PROCESSING "+ slide.word);
            let newZone;
            slide.component = ref;
            switch(slide.cat){
           
                case "translation" : newZone= new Region(slide); break;
                    case "Script" : newZone= new Region(slide); break;
                    case "Content" : newZone= new Glass(slide); break;
                 
            }
            
            if(!categories[slide.cat]){
                let selectedVal: boolean;
               
                
                selectedVal=true;
                
               categories[slide.cat] = {name: slide.cat, zoneCount:0, selected:selectedVal}; 
            }
            categories[slide.cat].zoneCount++;
            
            result.push(newZone);
           // console.log(result);
        });
           
      console.log(result);

           
           for(let c in categories){
               this.zoneCategories.push(categories[c]);
           }
           
        return result;
    }
    
    displayZones(){
        this.zones = this.zoneLoader;
        console.log("displaying zones");
    }

}
